    div.contact-section {
      div.form-container {
        width: 60vw;

        form {
          .submit-btn {
            width: 2rem;
            height: 2rem;
            right: 1.5rem;
            bottom: 0rem;
          }
        }
      }
      div.social-container {
        width: 20vw;

        a.ico-container {
          width: 5vw;
          height: 5vw;
        }
      }
      div.zing {
        height: 15vw;
        width: 15vw;
      }
    }
  }
}

